import React, { useEffect, useState } from 'react'
import './style.css'
import * as tick1 from '../../assets/images/Game_Main_Screen/tick 1.png';

import * as menu from '../../assets/Clean Walk/Video/VID_next.png'
import * as arrow from '../../assets/images/Game_Main_Screen/arrow.png';
import * as task1a from '../../assets/images/Game_Main_Screen/GM_button_01A.svg';
import * as task1b from '../../assets/images/Game_Main_Screen/GM_button_01B.svg';
import * as task2a from '../../assets/images/Game_Main_Screen/GM_button_02A.svg';
import * as task2b from '../../assets/images/Game_Main_Screen/GM_button_02B.svg';
import * as task3a from '../../assets/images/Game_Main_Screen/GM_button_03A.svg';
import * as task3b from '../../assets/images/Game_Main_Screen/GM_button_03B.svg';

import { useHistory, useParams } from 'react-router-dom';
import Spinner from 'components/Common/Spinner';
import { loader, SyncData } from 'components/Common/core';

import { levelstore } from '../../store/levelStore';

const TaskScreen = () => {
  const { level, loadlevel } = levelstore();

  const history = useHistory();
  const [spinner, setspinner] = useState(true)
  const [completed, setcompleted] = useState(false)
  const [list, setlist] = useState(
    {
      "Level_name": "-",
      "Task_Name": "-",
      "about": '-',
      "Description": "-",
      "image": "https://trtappfiles.s3.ap-south-1.amazonaws.com/Eco_CS+Files/LEVELS/LS_lev_01_on.png",
      "Location": "-",
      "taskdata": [
        { "number": 1, "Level": "Watch", "status": 'ACTIVE' },
        { "number": 2, "Level": "Activity", "status": 'LOCKED' },
        { "number": 3, "Level": "Quiz", "status": 'LOCKED' },
      ]
    }
  )
  console.log(">>>>>>>>>>>>>>>>", list);


  let params = useParams();

  useEffect(() => {

    if (!level) {
      loadlevel()
    }

    SyncData()

    if (localStorage.getItem('isFirsttime') === null) {
      loader()
    }    // setlist(listItems[Number(params.id) - 1])
    let Items = JSON.parse(String(localStorage.getItem('TaskData')));
    setlist(Items[Number(params.id) - 1])
    let a = JSON.parse(String(localStorage.getItem('EventStatusData')))
    // if (a !== null) {
    //   a.forEach((ele: any) => {
    //     if (ele.eventType === "Task" && ele.Level === params.id) {
    //       if (ele.Status === "LOCKED") {
    //         console.log("task is ", ele);
    //         // history.replace('/')
    //         /// need to navigate back to task listsceen
    //       }
    //       else
    //         if (ele.Status === "COMPLETED") {
    //           console.log("task is ", ele.Status);
    //           // history.replace({ pathname: '/RewardScreen/' + params.id, state: "FAST & JHIUTYYUT" })
    //           setspinner(false)
    //           // neeed to enable the rewardscreen button
    //           setcompleted(true)
    //         }
    //         else
    //           if (ele.Status === "UNLOCKED") {
    //             console.log("clue is ", ele.Status);
    //             setspinner(false)
    //             // Do NOtheing good to go
    //           }
    //     }
    //   });
    // }

  }, [])

  const leveldata = () => {

    const currentURL = window.location.href
    var temp = currentURL.split('/')
    var url_id = temp[temp.length - 1];

    let a = {
      "l_icon": "",
      "l_title": "",
      "l_body": "",
    };

    (level) && level.map((obj: any) => {
      // console.log(">> title >> : ", title, obj.l_title);

      if (String(url_id) === String(obj.l_level)) {
        // console.log(">> obj.l_icon >> : ", obj.l_icon);
        a = obj
      }
    })
    return a
  }

  return (
    <div style={{ backgroundColor: "#f1fdfd" }}>
      <div className="tasklist_topbanner">
        <div className="tasklist_banner">

          <div className="tasklist_bannermenu" onClick={() => {
            history.replace('/TaskListScreen')
          }}>
            <img className='tasklist_menuicon rotateimg180' src={menu} />
          </div>

          <div className="tasklist_bannerimg">
            <img className='tasklist_level1' src={leveldata().l_icon} />
          </div>

          <div className="tasklist_bannertext">
            <div className="tasklist_sub-text1">{leveldata().l_title}</div>
            {/* <div className="tasklist_sub-text2">Location: {list.Location}</div> */}
          </div>

          {((list.taskdata[0].status === "DONE") && (list.taskdata[1].status === "DONE") && (list.taskdata[2].status === "DONE")) ?
            <div className="tasklist_reward_icon_container" onClick={() => {
              history.replace('/RewardScreen/' + params.id)
            }}>
              <img className='tasklist_banner_rewardicon' src={require("../../assets/Clean Walk/Congratulations/medal" + params.id + ".png")} />
            </div> : null
          }
        </div>
      </div>

      <div className="tasklist_bottommaincontainer">
        <div className="tasklist_about">
          <p className="taskscreen_description">
            {leveldata().l_body}
          </p>
        </div>
        {/* {spinner ?
          <div style={{
            width: '100vw',
            height: '100vh',
            position: 'absolute',
            top: '0',
            zIndex: 1000,
            backgroundColor: '#00243a',
          }}>
            <Spinner />
          </div> :
          null} */}
        <div className="tasklist_listitem_container">

          <div className={(list.taskdata[0].status === "LOCKED") ? "tasklist_listitem_disabled" : "tasklist_listitem"} onClick={() => {
            // if (list.taskdata[0].status === "ACTIVE")
            history.replace('/VideoScreen/' + params.id)
            console.log('======================>', list.taskdata[0].active)
          }}>
            <div className="tasklist_sub_level_container">
              {/* <img className='tasklist_sub_level1' src={gm1s} /> */}
              <div className='tasklist_sub_level_subcontainer1'>
                <div className="tasklist_listnumber">{list.taskdata[0].number}</div>
                <div className="tasklist_listtext">{list.taskdata[0].Level}</div>
                {(list.taskdata[0].status === "DONE") ? <img className='tasklist_button' src={task1a} /> : <img className='tasklist_button' src={task1b} />}
              </div>
              {(list.taskdata[0].status === "DONE") ? <img className='tasklist_tick' src={tick1} /> : null}
              {(list.taskdata[0].status === "ACTIVE") ? <img className='tasklist_arrow' src={arrow} /> : null}
            </div>
          </div>

          <div className={(list.taskdata[1].status === "LOCKED") ? "tasklist_listitem_disabled" : "tasklist_listitem"} onClick={() => {
            // if (list.taskdata[1].status === "ACTIVE")
            history.replace('/ActivityScreen/' + params.id)
            console.log('======================>', list.taskdata[1].active)
          }}>
            <div className="tasklist_sub_level_container">
              {/* <img className='tasklist_sub_level1' src={gm1s} /> */}
              <div className='tasklist_sub_level_subcontainer2'>
                <div className="tasklist_listnumber">{list.taskdata[1].number}</div>
                <div className="tasklist_listtext">{list.taskdata[1].Level}</div>
                {(list.taskdata[1].status === "DONE") ? <img className='tasklist_button' src={task2a} /> : <img className='tasklist_button' src={task2b} />}
              </div>
              {(list.taskdata[1].status === "DONE") ? <img className='tasklist_tick' src={tick1} /> : null}
              {(list.taskdata[1].status === "ACTIVE") ? <img className='tasklist_arrow' src={arrow} /> : null}
            </div>
          </div>

          <div className={(list.taskdata[2].status === "LOCKED") ? "tasklist_listitem_disabled" : "tasklist_listitem"} onClick={() => {
            // if (list.taskdata[2].status === "ACTIVE")
            history.replace('/QuizScreen/' + params.id)
            console.log('======================>', list.taskdata[2].active)
          }}>
            <div className="tasklist_sub_level_container">
              {/* <img className='tasklist_sub_level1' src={gm1s} /> */}
              <div className='tasklist_sub_level_subcontainer3'>
                <div className="tasklist_listnumber">{list.taskdata[2].number}</div>
                <div className="tasklist_listtext">{list.taskdata[2].Level}</div>
                {(list.taskdata[2].status === "DONE") ? <img className='tasklist_button' src={task3a} /> : <img className='tasklist_button' src={task3b} />}
              </div>
              {(list.taskdata[2].status === "DONE") ? <img className='tasklist_tick' src={tick1} /> : null}
              {(list.taskdata[2].status === "ACTIVE") ? <img className='tasklist_arrow' src={arrow} /> : null}
            </div>
          </div>

        </div>

        <div className="bgbottombluebar" />

      </div>
    </div>
  )
}


export default TaskScreen
